import axios from 'axios'
import appRouter from '../router'
import { getToken } from '@/plugins'
import Vue from 'vue'

/**
 * @description axios&
 */
const http = axios.create({
  baseURL: '/api',
  timeout: 1000 * 30,
  responseType: 'json',
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json;charset=UTF-8'
  }
})

/**
 * @description &
 */
const Cancel = axios.CancelToken
let pending = [] // arrayajaxajax
let removePending = config => {
  for (let i in pending) {
    // 
    if (pending[i].u === config.url + '&' + config.method) {
      // 
      pending[i].f()
      // 
      pending.splice(i, 1)
    }
  }
}

/**
 * @description 
 */
http.interceptors.request.use(
  config => {
    // 
    if (!navigator.onLine) {
      Vue.prototype.$toast('')
      return false
    }
    // ajax
    removePending(config)
    config.cancelToken = new Cancel(c => {
      // +ajax
      pending.push({
        u: config.url + '&' + config.method,
        f: c
      })
    })

    if (getToken()) {
      config.headers.token = getToken()
    }

    return config
  },
  error => {
    return Promise.reject(error)
  }
)

/**
 * @description 
 */
http.interceptors.response.use(
  response => {
    // ajaxpending
    removePending(response.config)

    // 
    if (response && response.status === 200) {
      if (response.data && response.data.resultCode === '000000') {
        return response.data
      } else if (response.data && response.data.msg) {
        Vue.prototype.$toast(response.data.msg)
        return
      } else {
        return
      }
    }
    Vue.prototype.$toast('')
  },
  error => {
    // 
    if (
      error &&
      error.response &&
      error.response.status &&
      error.response.status === 401
    ) {
      // 
      Vue.prototype.$toast('')
      window.localStorage.clear()
      setTimeout(() => {
        appRouter.replace({ path: '/login' })
      }, 2500)
      return
    } else if (
      error &&
      error.response &&
      error.response.status &&
      error.response.status === 403
    ) {
      // 
      appRouter.replace({ path: '/notFound' })
      return
    } else if (
      error &&
      error.response &&
      error.response.status &&
      error.response.status === 504
    ) {
      Vue.prototype.$toast('')
      return
    }
    // return Promise.reject(error)
    // 
    return { data: {} }
  }
)

/**
 * @description 
 * @param {} methods
 * @param {} url
 * @param {} params
 * @param {} config
 */
export default function server (
  methods = 'get',
  url = '/',
  params = {}
) {
  // 
  params.timeStamp = new Date().getTime()
  let m = methods.toLowerCase()
  switch (m) {
    case 'get':
      return http.get(url, { params })
    case 'post':
      return http.post(url, params)
    case 'put':
      return http.put(url, params)
    case 'delete':
      return http.delete(url, { params })
  }
}